@use "../banner";
@use "components/properties";

/* THEME CONFIG VARS */

:root {
  /* Animate line under links: none | initial */
  --decorate-links: initial;
  // none | underline
  --decorate-links-underline: none;
  // nowrap | wrap
  --decorate-links-wrap: nowrap;

  // Topnav
  --topnav-shadow: hsla(0 0% 0% / 0.1) 0 0.5rem 1rem;
  // Amount of space to leave at top when scrolling to sections or anchor
  --scroll-padding-top: 100px;

  // Circle
  --adorn-primary-mask-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="200" height="200"><circle cx="100" cy="100" r="78" fill="transparent" stroke="black" stroke-width="32px" /></svg>');
  // Dot grid
  --adorn-secondary-mask-image: url('data:image/svg+xml,<svg width="144" height="144" xmlns="http://www.w3.org/2000/svg"><pattern id="dots" patternUnits="userSpaceOnUse" width="12" height="12"><circle cx="6" cy="6" r="1.5" fill="black"/></pattern><rect width="100%" height="100%" fill="url(%23dots)"/></svg>');
  // Arrow
  --adorn-arrow-mask-image: url('data:image/svg+xml,<svg viewBox="0 0 188 188" xmlns="http://www.w3.org/2000/svg"><path d="m81 92 5-38a5 5 0 0 0-8-5L2 116l-1 1c-2 2-1 5 1 7l76 63 4 1c3-1 5-3 4-6l-5-31c29-5 57-20 77-43 17-18 28-42 27-71 0-10-3-22-7-34a5 5 0 0 0-9 1c-5 37-19 58-37 71-15 11-34 15-51 17Z" fill-rule="evenodd"/></svg>');

  /* Secion max width */
  --section-width: 1170px;
  --section-padding: 24px;
  --section-padding-mobile: 12px;

  /* Shadows */
  // Drop shadow 10%
  --theme-card-flipper-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
  --theme-card-shadow: 0 6px 24px 0 rgba(27, 27, 27, 0.04);
  --theme-card-shadow2:  0 6px 24px 0 rgba(27, 27, 27, 0.2);

  /* Popup */
  --theme-popup-close-bg-color: var(--theme-button-primary-bg-color, black);
  --theme-popup-close-color: var(--theme-button-primary-text-color, white);
  --theme-popup-border-color: var(--theme-secondary-color, transparent);

  /* Buttons */
  /* Primary buttons are fully themed. Buttons in forms inherit primary button styles. */
  // Primary button colors are defined in global custom values
  --theme-button-primary-shadow: 0 10px 25px 0 rgba(0, 0, 0, 0.21);
  // Secondary button
  --theme-button-secondary-text-color: var(--theme-text-headline-color, #000);
  --theme-button-secondary-bg-color: var(--theme-background-1-color, #fff);
  --theme-button-secondary-shadow: 0 6px 14px rgba(0, 0, 0, 0.02),
    0 1px 4px rgba(27, 27, 27, 0.02), 0 4px 14px rgba(25, 25, 25, 0.06);
  // Link button
  --theme-button-link-text-color: var(--theme-text-headline-color, #000);
  --theme-button-link-bg-color: transparent;

  /* Forms */
  --theme-input-bg-color: #f5f5f5;
  --theme-input-text-color: black;
  /* Surveys */
  --theme-survey-bg: #fff;
  --theme-survey-input-bg-color: var(--theme-input-bg-color);
  --theme-survey-label-color: black;
  --theme-survey-padding-inline: 40px;
  --theme-survey-progressbar-opacity: 0.2;

  /* Chat Widget */
  --theme-chat-widget-primary-color: var(--theme-primary-color, #188bf6);
  --theme-chat-widget-active-color: var(--theme-primary-color, #188bf6);
  --theme-chat-widget-bubble-color: var(--theme-primary-color, #188bf6);
  --theme-message-outgoing-text-color: #fff;
  --theme-message-incoming-color: #e4e9f0;
  --theme-message-incoming-text-color: #333;
  --theme-conversation-background-color: #fff;
  --theme-message-check-mark-color: #399bf0;
  --theme-message-padding-content: 1em;
  --theme-message-padding-between: 0;
  --theme-message-round-size: 0.5em;
  --theme-message-round-size-negative: calc(
    -1 * var(--theme-message-round-size)
  );

  /* Cookie Consent Banner */
  --theme-cookie-banner-bg-color: var(--theme-attention-color, #000);
  --theme-cookie-banner-text-color: var(--theme-text-dark-bg-color, #eee);
  --theme-cookie-banner-link-color: var(--theme-text-dark-bg-color, #eee);
}

/* Override the GHL default vars */
// See js/defaults.ts to use --global vars to override GHL css vars with Custom Values
:root {
  /* Text Colors */
  --text-color: var(--theme-text-content-color);
  --link-color: var(--theme-text-link-color);
  /* Add additional overrides here as needed */
}

/*
  CUSTOM VALUE VARIABLES
  These variables ar set from GHL custom values in a style block
  in the themplate's hidden Global Code block on each page.
*/
:root {
  /* Theme Colors */

  /* [CustomValue] COLOR: Primary */
  /* {{ custom_values.color_primary }} */
  --theme-primary-color: var(--color-lt985yd7);

  /* [CustomValue] COLOR: Secondary */
  /* {{ custom_values.color_secondary }} */
  --theme-secondary-color: var(--color-lt9ca2ns);

  /* [CustomValue] COLOR: Accent */
  /* {{ custom_values.color_accent }} */
  --theme-accent-color: var(--color-luhfgx7m);

  /* [CustomValue] COLOR: Attention */
  /* {{ custom_values.color_attention }} */
  --theme-attention-color: var(--color-luhfsans);

  /* Text Colors */
  /* [CustomValue] COLOR: Text Headline */
  /* {{ custom_values.color_text_headline }} */
  --theme-text-headline-color: var(--color-lt9cay4k);

  /* [CustomValue] COLOR: Text Content */
  /* {{ custom_values.color_text_content }} */
  --theme-text-content-color: var(--color-lt9cbrrr);

  /* [CustomValue] COLOR: Text Link */
  /* {{ custom_values.color_text_link }} */
  --theme-text-link-color: var(--color-lt9ceg80);

  /* [CustomValue] COLOR: Text Dark BG */
  /* {{ custom_values.color_text_content_dark_bg }} */
  --theme-text-dark-bg-color: var(--color-lt9cf6ne);

  /* Icon Colors */
  /* [CustomValue] COLOR: Icon */
  /* {{ custom_values.color_icon }} */
  --theme-icon-color: var(--color-lt9cg14m);

  // Primary Button is used for setting form buttons

  /* [CustomValue] COLOR: Button Primary Text */
  /* {{ custom_values.color_button_primary_text }} */
  --theme-button-primary-text-color: var(--color-luhh4ycy);

  /* [CustomValue] COLOR: Button Primary BG */
  /* {{ custom_values.color_button_primary_bg }} */
  --theme-button-primary-bg-color: var(--color-luhgqfvg);

  /* Background Colors */
  /* [CustomValue] COLOR: Background #1 */
  /* {{ custom_values.color_background_1 }} */
  --theme-background-1-color: var(--color-lt9cidqc);

  /* [CustomValue] COLOR: Background #2 */
  /* {{ custom_values.color_background_2 }} */
  --theme-background-2-color: var(--color-lt9ciq7x);

  /* [CustomValue] COLOR: Background Dark */
  /* {{ custom_values.color_background_dark }} */
  --theme-background-dark-color: var(--color-lt9cj7fg);
}
